@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonials-blog {
	margin-top: 5rem;
	@include m.desktop {
		@include m.grid(3, 4rem);
	}

	&__blog {
		@include m.desktop {
			grid-column: 2/4;
		}
	}

	&__heading {
		@include m.heading(true);
	}
}

.testimonial {
	text-align: center;
	&__image-container {
	}

	&__image {
		width: 20rem;
		margin: 0 auto;
	}
	&__content {
		margin-top: -10rem;
		background-color: v.$white;
		padding: 10rem 2rem 2rem 2rem;
	}
	&__name {
		font-weight: 700;
	}
	&__text {
		&::before {
			content: '';
			background-image: url(../img/quote.svg);
			background-repeat: no-repeat;
			background-position: center;
			margin-right: 1rem;
			display: inline-block;
			width: 4rem;
			height: 4rem;
		}
	}
}

.entry {
	background-color: v.$white;
	margin-bottom: 2rem;

	@include m.tablet {
		@include m.grid(5, 2rem);
		align-items: center;
	}
	&__image-container {
		@include m.tablet {
			grid-column: 1 / 3;
		}
	}
	&__image {
		height: 30rem;
		object-fit: cover;
		width: 100%;
	}
	&__content {
		padding: 1rem;
		@include m.tablet {
			grid-column: 3 / 6;
		}
	}

	&__meta {
		font-size: 1.4rem;
	}

	&__link {
		@include m.button(v.$primary, v.$white);
		display: block;
		@include m.tablet {
			display: inline-block;
		}
	}
}
